<template>
  <div>
    <topHeader></topHeader>
    <div class="categories"></div>

    <ul>
      <li v-for="msg in getCategories" :key="msg.Id">{{ msg }}</li>
    </ul>
  </div>
</template>

<script>
import topHeader from "./Header";
//import pict from "./Picture";
export default {
  components: {
    topHeader
    // pict
  },
  methods: {
    getCategories() {
      this.$http.get("/api/category").then(result => {
        return result.data;
      });
    }
  }
};
</script>

